import React, { useEffect, useState, createRef } from 'react'
import classNames from 'classnames'
import {
  CRow,
  CCol,
  CCard,
  CCardHeader,
  CCardBody,
  CBadge,
  CDataTable,
  CButton,
  CFormGroup,
  CLabel,
  CInput,
  CInputCheckbox,
  CSelect,
  CInputFile,
  CAlert
} from '@coreui/react'
import { rgbToHex } from '@coreui/utils'
import { DocsLink } from 'src/reusable';
import usersData from '../../users/UsersData'
import ModalForm from '../../../components/ModalForm/ModalForm';
import {getPosts, createPost, deletePost, updatePost} from '../../../services/forumService';
import {getCategories} from '../../../services/categoriesService';
import ModalWarning from '../../../components/ModalWarning/ModalWarning';



const getBadge = highlight => {
  switch (highlight) {
    case true: return 'success'
    case false: return 'danger'
    default: return 'primary'
  }
}
const fields = [{key: 'name', value: 'nombre', sorter: true},'description', 'content', 'highlight', 'actions']
const fields1 = { name:'Nombre', description: 'Descripcion', content: 'Contenido', highlight: 'Destacado', actions:'Acciones', createdAt: 'Fecha'}
const columnsFilter = { name:'Nombre', description: 'Descripción', financial_knowledge: 'Conocimiento Financiero', value: 'valor', highlight: 'Destacado' }
// const fields2 = ['name']

const ForumTopics = ({history}) => {
  const [visibleForm, setVisibleForm] = useState(false);
  const [deleteRow, setDeleteRow] = useState(false);
  const [editing, setEditing] = useState(false)
  const [error, setError] = useState(false)
  const [errorFile, setErrorFile] = useState(false)
  const [idForDelete, setIdForDelete] = useState(null);
  const [data, setData] = useState([]);
  const [categories, setCategories] = useState([]);
  const [isHighlight,setIsHighlight] = useState(false);
  const [loading, setLoading] = useState(null);
  const [values, setValues] = useState({
    name: '',
    description: '',
    highlight: isHighlight,
    category: '',
    image: '',
    content: ''
  });

  const toggle = ()=>{
    setEditing(false);
    setVisibleForm(!visibleForm);
    setErrorFile(false);
    setValues({
        name: '',
        description: '',
        highlight: '',
        category: '',
        image: '',
        content: '',
        clean: visibleForm ? true : false,
    })
}
 useEffect (() => {
  handleLogout();
  getData();
  getCategoriesData();
},[])

const handleLogout = async () => {
    const token =  await localStorage.getItem('apitoken');
    if(token===null || token === undefined){
      history.push('/login');
    }
   }

 const getData = async () => {
   try {
    setLoading(true);
    const response = await getPosts();
    await setData(response.data.reverse());
    if(response.data.length > 0) await setLoading(false);
   } catch(err){
     console.log(err)
   }
 }
 const getCategoriesData = async () => {
  try {
   setLoading(true);
   const response = await getCategories();
   await setCategories(response.data);
   if(response.data.length > 0) await setLoading(false);
  } catch(err){
    console.log(err)
  }
}


const storePost = async () => {
  let post = new FormData();
  if(values.name==='' ||
    values.description ==='' ||
    values.content==='' ||
    values.highlight==='' ||
    values.image==='' ||
    values.category === ''
    ){
      setError(true)
      setTimeout(()=> {
        setError(false);
      },6000)
  }else{
    post.append('name', values.name);
    post.append('description', values.description);
    post.append('content', values.content);
    post.append('category', values.category);
    post.append('highlight', values.highlight);
    post.append('image', values.image);
  try {
      const response = await createPost(post)
      if(response) {
          console.log(response.data, 'post creado');
          setVisibleForm(!visibleForm);
          await getData();
          setError(false)
      }
  } catch(err){
      console.log(err)
  }
}
}

const deleteItem = (id) => {
  setDeleteRow(!deleteRow);
  setIdForDelete(id)
}

const handleDelete = async (id) => {
   console.log(id,'si entra al delete')
   try {
  if(id!==null){
  await deletePost(id)
}
  } catch (err) {
      console.log(err);
  }
  setDeleteRow(!deleteRow);
  await getData();
}

const handleEdit = (item) => {

  setVisibleForm(!visibleForm);
  setEditing(true);
  setError(false);
  setValues({...values,
      id: item.id,
      name: item.name,
      description: item.description,
      highlight: item.highlight,
      category: item.category,
      content: item.content,
      clean: visibleForm ? true : false,
      // image: '',
    })

}

const update = async () => {
  let post = new FormData();
  post.append('name', values.name);
  post.append('description', values.description);
  post.append('content', values.content);
  post.append('category', values.category);
  post.append('highlight', values.highlight);
  if(values.image!=='')post.append('image', values.image);
    if(values.id !== '' && values.name!=='' && values.description !==''  &&
    values.content!==''  &&
    values.highlight!==''  &&
    values.category !==''){
        try {

          const response = await updatePost(post, values.id)
          if(response) {
              console.log(response.data, 'post actualizado');
              setVisibleForm(!visibleForm);
              await getData();
              setEditing(false);
          }
        } catch(err) {
            console.log(err);
            setError(true);

        }
    } else {
        setError(true);
        setTimeout(()=> {
          setError(false);
        },6000)
    }
}

const handleChange = (e) => {
  e.preventDefault();
  setValues({...values,
    [e.target.name] : e.target.value
  })
  console.log(values);

}
const handleCheck = (e) => {
  e.preventDefault();
  setIsHighlight(!isHighlight)
  setValues({...values,
    highlight: isHighlight
  })
  console.log(values);

}

const filterCombo = () => {
  let filtered = categories.filter(item => item.id === values.category);
  return filtered.name;
}

const changeFile = (e) => {
  var file = e.target.files[0],
      pattern = /^image/,
      reader = new FileReader();

      if(!pattern.test(file.type)) {
          setErrorFile(true);
          console.log(e.target.files[0].name)
          // eslint-disable-next-line no-unreachable
          setValues({...values,
            image: '',
            clean: visibleForm ? true : false,
          })
          return;
      } else{
        setErrorFile(false);

  reader.onload = upload => {
  setValues({...values,
  image: upload.target.result
  });

  console.log(values);
}
reader.readAsDataURL(file);
    console.log(values.image);
    console.log("Uploaded");

console.log(values.image);
      }
}

  return (
    <>
      <CCard>
        <CCardHeader>
          <CButton
          color="primary"

      onClick={() => toggle()}
      className="mr-1"
    >Agregar Publicación</CButton>
        </CCardHeader>
        <CCol xs="12" lg="12">
          <CCard>
            <CCardHeader>
              Temas de Foro
            </CCardHeader>
            <CCardBody>
            <CDataTable
              items={data}
              fields={fields}
              columnHeaderSlot={fields1}
              itemsPerPage={10}
              tableFilter={{label: 'Buscar', placeholder:'Ingresa tu Busqueda'}}
              loading={loading}
              pagination
              striped
              responsive
              scopedSlots = {{
                'highlight':
                  (item)=>(
                    <td>
                      <CBadge color={getBadge(item.highlight)}>
                        {item.highlight == true ? 'Si' : 'No'}
                      </CBadge>
                    </td>
                  ),
                  'actions':
              (item) => (
                <td>
                  <CButton style={{marginRight: 10}} onClick={() => handleEdit(item)} color="warning">Editar</CButton>
                  <CButton onClick={() => deleteItem(item.id)} color="danger">Eliminar</CButton>
                </td>
              ),
              'name':
              (item) => (
              <td style={{maxHeight:20, height: 20, maxWidth: 90}}>{`${item.name}`}</td>
              ),
              'description':
              (item) => (
              <td style={{maxHeight:20, height: 20, maxWidth: 90}}>{`${item.description}`}</td>
              ),
              'content':
              (item) => (
              <td style={{maxHeight:20, height: 20, maxWidth: 90}}>{`${item.content}`}</td>
              ),

              }
            }
            />
            </CCardBody>
          </CCard>
        </CCol>
      </CCard>
      <ModalForm visible={visibleForm} modalToggle={toggle} onSubmit={!editing ? () => storePost() : () => update()} title="Publicacion" >
      <CCol xs="12" sm="12">
          <CCard>
            <CCardBody>
              <CFormGroup>
                <CLabel htmlFor="name">Nombre</CLabel>
                <CInput id="name" name="name" value={values.name} onChange={(e) => handleChange(e)} placeholder="Nombre del curso" />
              </CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="description">Descripción</CLabel>
                <CInput id="description" name="description" value={values.description} onChange={(e) => handleChange(e)} placeholder="Descripción" />
              </CFormGroup>
              <CFormGroup>
              <CFormGroup>
                <CLabel htmlFor="content">Contenido</CLabel>
                <CInput type="textarea" name="content" onChange={(e) => handleChange(e)} value={values.content} id="content" placeholder="Contenido" />
              </CFormGroup>
                <CLabel htmlFor="category">Categoría</CLabel>
                <CSelect onChange={(e) => handleChange(e)} value={values.category} custom name="category" id="category">
                        <option value=''>'Selecciona'</option>
                      {categories.map(item => (
                        <option value={item.id}>{item.name}</option>
                      ))}
                    </CSelect>
              </CFormGroup>
              <CFormGroup row>
                  <CCol md="3">
                    <CLabel> ¿Destacado?</CLabel>
                  </CCol>
                  <CCol md="9">
                    <CFormGroup variant="custom-checkbox" inline>
                    <CSelect onChange={(e) => handleChange(e)} value={values.highlight} custom name="highlight" id="highlight">
                 <>
                      <option value=""></option>
                      <option value={true}>Si</option>
                      <option value={false}>No</option> </>

                    </CSelect>
                    </CFormGroup>
                  </CCol>
                </CFormGroup>
              <CFormGroup key={values.clean} row>
                  <CLabel col htmlFor="image">Imagen</CLabel>
                  <CCol xs="12" md="12">
                    <CInputFile encType="multipart/form-data" onChange={(e) => changeFile(e)} id="image" name="image"/>
                  </CCol>
                </CFormGroup>
                { error ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Debes ingresar todos los campos!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
                { errorFile ? <CCol>
                    <CFormGroup>
                        <CAlert color="danger">
                         Formato de archivo inválido, solo imágenes!
                        </CAlert>
                    </CFormGroup>
                </CCol>: null}
            </CCardBody>
          </CCard>
        </CCol>
      </ModalForm>
      <ModalWarning visible={deleteRow} onSubmit={() => handleDelete(idForDelete)} modalToggle={() => deleteItem(null)} title="Eliminar Usuario">Estas seguro de eliminar este Usuario?</ModalWarning>
    </>
  )
}

export default ForumTopics
